<template>
    <div>
        <div id="user_data" class="container" >
            <!--信息不完整下补全信息-->
            <div class="panel panel-info" v-if="!indexData">
                <div class="panel-heading">
                    <h2 class="panel-title">补全资料</h2>
                </div>
                <div class="panel-body">
                    <h4 class="text-primary">请补全您的信息，方便获取系统更多的功能</h4>
                </div>

                <form class="form form-horizontal" name="myaddForm" action="../addData.action" method="post" ref="myaddForm">
                  <!--错误显示-->
                <div class="form-group"></div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">邮箱</label>
                        <div class="col-sm-10" :class="{'has-success':!errors.has('email')&&userAdd.email!='','has-error':errors.has('email')}">
                            <div class="col-sm-6" >
                                <input type="text" class="form-control" v-model="userAdd.email" name="email" v-validate="'required|email'" data-vv-as="邮箱">
                            </div>
                            <div class="col-sm-4 text-alert text-danger" >
                               <span v-show="errors.has('email')">{{errors.first("email")}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">姓名</label>
                        <div class="col-sm-10" :class="{'has-success':!errors.has('name')&&userAdd.name!='','has-error':errors.has('name')}">
                            <div class="col-sm-6">
                                <input type="text" class="form-control" v-model="userAdd.name" name="name" v-validate="'required|regex:^[\u4e00-\u9fa10a-zA-Z]+$'" data-vv-as="姓名">
                            </div>
                            <div class="col-sm-4 text-alert text-danger" >
                                 <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">身份证号</label>
                        <div class="col-sm-10" :class="{'has-success':!errors.has('idnumber')&&userAdd.idnumber!='','has-error':errors.has('idnumber')}">
                            <div class="col-sm-6">
                                <input type="text" class="form-control" v-model="userAdd.idnumber" name="idnumber" v-validate="'required|idnumber'" data-vv-as="身份证号">
                            </div>
                            <div class="col-sm-4 text-alert text-danger">
                                <span v-show="errors.has('idnumber')">{{errors.first("idnumber")}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group">
                        <label for="roleselect" class="control-label col-sm-2">角色选择审核</label>
                        <div class="col-sm-10">
                            <div class="col-sm-6">
                                <select name="role" id="roleselect" v-model="userAdd.role" class="form-control">
                                    <option value="" disabled>请选择角色</option>
                                    <option  v-for="item in selectdata.role" :key="item.id" :value="item.id" v-text="item.name"></option>
                                </select>
                            </div>
                            <div class="col-sm-4 text-alert text-danger">
                                <span v-show="errors.has('role')">{{errors.first("role")}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="form-group"><button type="button" class="btn btn-info col-sm-2 col-sm-offset-4" @click="submitForm"
                            >提 交</button></div>
                </form>
            </div>
            <!--信息完整下显示信息-->
            <div class="panel panel-info" v-if="indexData">
                <div class="panel-heading">
                    <h2 class="panel-title">基本资料</h2>
                </div>
                <div class="panel-body">
                    <div class="col-sm-12">
                        <div class="col-sm-1 pull-right ">
                            <button type="button" class=" btn btn-info" @click="upDate=!upDate;loginData()">编辑资料</button>
                        </div>
                        <div class="col-sm-1 pull-right">
                            <button ype="button" class="btn btn-info" @click="upDatePass=!upDatePass;loginData()">修改密码</button>
                        </div>
                        <div class="col-sm-1 pull-right">
                            <button ype="button" class="btn btn-info" @click="upDateRole=!upDateRole;loginData()">角色审核</button>
                        </div>
                    </div>
                    <div class="col-sm-12">
                        <ul class="nav center-block nav-list">
                            <li><span class="col-sm-2">帐号:</span><span v-text="userData.id"></span></li>
                            <li><span class="col-sm-2">昵称:</span><span v-text="userData.nic"></span></li>
                            <li><span class="col-sm-2">姓名:</span><span v-text="userData.name"></span></li>
                            <li><span class="col-sm-2">电话:</span><span v-text="userData.tel"></span></li>
                            <li><span class="col-sm-2">邮箱:</span><span v-text="userData.email"></span></li>
                            <li><span class="col-sm-2">站区:</span><span v-text="userData.depart"></span></li>
                            <li><span class="col-sm-2">角色:</span><span v-text="userData.role"></span></li>
                            <li><span class="col-sm-2">身份证号:</span><span v-text="userData.idnumber"></span></li>
                            <li><span class="col-sm-2">注册时间:</span><span >{{userData.regtime,'yyyyMMdd'|datafilter}}</span></li><!--| date:'yyyy年MM月dd日'-->
                        </ul>
                    </div>
                </div>
                <div class="back_box" v-if="upDateRole">
                    <div class="panel panel-info col-sm-6 col-sm-offset-3 ">
                        <div class="panel-heading">
                            <h2 class="panel-title">角色审核</h2>
                            <span class="close glyphicon glyphicon-remove" @click="upDateRole=!upDateRole"></span>
                        </div>
                        <form class="form form-horizontal panel-body">
                            <div class="form-group">
                                <label for="updaterole" class="col-sm-3 control-label">选择角色</label>
                                <div class="col-sm-8">
                                    <select name="updaterole" id="updaterole" v-model="updateData.role" class="form-control">
                                        <!-- <option value="" disabled v-text="userData.role"></option> -->
                                        <option v-for="item in selectdata.role" :key="item.id" :value="item.id" v-text="item.name" ></option>
                                    </select>
                                </div>
                            </div>
                            <div class="form-group">
                                    <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-4" @click="UpdateData('updateRole')">提 交</button>
                                </div>
                        </form>
                    </div>
                </div>
                <div class="back_box " v-if="upDatePass">
                        <div class="panel panel-info col-sm-6 col-sm-offset-3 ">
                            <div class="panel-heading">
                                <h2 class="panel-title">修改信息</h2>
                                <span class="close glyphicon glyphicon-remove"  @click="upDatePass=!upDatePass"></span>
                            </div>
                            <form class="form form-horizontal panel-body" name="myFormPass">
                                <div class="form-group">
                                    <label class="col-sm-3 control-label">旧密码</label>
                                    <div class="col-sm-8" :class="{'has-success':!errors.has('myoldPass')&&updateData.oldPass!=null,'has-error':errors.has('myoldPass')}">
                                        <div class="col-sm-12"><input type="password" name="myoldPass" v-model="updateData.oldPass" placeholder="请输入旧密码" class="form-control"
                                                 data-vv-as="密码" v-validate="'required'"></div>
                                    </div>
                                    <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                                        <span v-show="errors.has('myoldPass')">{{errors.first('myoldPass')}}</span>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <label class="col-sm-3 control-label">新密码</label>
                                    <div class="col-sm-8" :class="{'has-success':!errors.has('newPass')&&updateData.newpass!=null,'has-error':errors.has('newPass')}">
                                        <div class="col-sm-12"><input type="password" v-model="updateData.newpass" name="newPass"  placeholder="请输入新密码" class="form-control"
                                                 data-vv-as="密码" v-validate="'required|password'">
                                            </div>
                                    </div>
                                    <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                                        <span v-show="errors.has('newPass')">{{ errors.first('newPass')}}</span>
                                    </div>
                                    <div class="col-sm-10 col-sm-offset-3" style="font-size:8px;">密码为字母、数字和符号两种及以上的组合，6-16个字符</div>
                                </div>
                                <div class="form-group">
                                    <label class="col-sm-3 control-label">确认密码</label>
                                    <div class="col-sm-8" :class="{'has-success':!errors.has('mynewPass')&&updateData.newpass==updateData.newPass&&updateData.newPass!=null,'has-error':errors.has('mynewPass')||updateData.newpass!=updateData.newPass}">
                                        <div class="col-sm-12"><input type="password" name="mynewPass" v-model="updateData.newPass" placeholder="请再次输入新密码"
                                                class="form-control" data-vv-as="密码" v-validate="'required'"></div>
                                    </div>
                                    <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                                    <span v-show="errors.has('mynewPass')">{{ errors.first('mynewPass')}}</span>
                                <span v-show="updateData.newpass!=updateData.newPass&&updateData.newPass!=null">两次密码不符</span>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-4" @click="UpdateData('updatePwd')">提 交</button>
                                </div>
                            </form>
                        </div>
                </div>
                <div class="back_box " v-if="upDate">
                    <div class="panel panel-info col-sm-6 col-sm-offset-3">
                        <div class="panel-heading">
                            <h2 class="panel-title">修改信息</h2>
                            <span class="close glyphicon glyphicon-remove" @click="upDate=!upDate"></span>
                        </div>
                        <form class="form form-horizontal panel-body" name="myForm">
                            <div class="form-group">
                                <label class="col-sm-3 control-label">昵称</label>
                                <div class="col-sm-8" :class="{'has-success':!errors.has('myNic')&&updateData.nic!=null,'has-error':errors.has('myNic')}">
                                    <div class="col-sm-12"><input type="text" name="myNic"   class="form-control"  v-model="updateData.nic"
                                            required data-vv-as="昵称" v-validate="'required|cnlength:12'"></div>
                                </div>
                                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger">
                                   <span v-show="errors.has('myNic')">{{errors.first('myNic')}}</span>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="col-sm-3 control-label">姓名</label>
                                <div class="col-sm-8" :class="{'has-success':!errors.has('myName')&&updateData.name!=null,'has-error':errors.has('myName')}">
                                        <div class="col-sm-12"><input type="text" name="myName"   class="form-control" v-model="updateData.name"
                                                v-validate="'required|regex:^[\u4e00-\u9fa10a-zA-Z]+$'" data-vv-as="姓名">
                                        </div>
                                </div>
                                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger " >
                                    <span v-show="errors.has('myName')">{{errors.first("myName")}}</span>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="col-sm-3 control-label">邮箱</label>
                                <div class="col-sm-8" :class="{'has-success':!errors.has('myEmail')&&updateData.email!=null,'has-error':errors.has('myEmail')}">
                                    <div class="col-sm-12"><input type="email" name="myEmail" class="form-control" v-model="updateData.email" required 
                                        data-vv-as="邮箱" v-validate="'required|email'">
                                    </div>
                                </div>
                                <div class="col-sm-10 col-sm-offset-2 text-alert control-label text-danger ">
                                   <span v-show="errors.has('myEmail')">{{errors.first('myEmail')}}</span>
                                </div>
                            </div>
                            <div class="form-group">
                                <button type="button" class="btn btn-info  col-sm-3 col-sm-offset-4" @click="UpdateData('updateDate')">提 交</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      indexData: true, //默认为false
      userAdd: {
        role: "",
        depart: "",
        idnumber: "",
        name: "",
        email: ""
      },
      selectdata: {
        role: [{id:1,name:1}],
        depart: []
      },
      upDate: false,
      upDatePass: false,
      upDateRole: false,
      userData: {
        id: "123",
        nic: "123",
        name: "123",
        tel: "123",
        email: "123",
        depart: "123",
        role: "132",
        idnumber: "123",
        regtime: '1532063375000'
      },
      updateData: {
          
      }
    };
  },
  mounted: function() {
    var vm = this;
    vm.initData(vm);
  },
  methods: {
      //数据提交事件
      submitForm(){
            var vm=this;
            var userAdd=vm.userAdd;
            vm.$validator.validateAll().then(function(value){
                if(value){             
                    if(userAdd.role!=""){
                        vm.$refs.myaddForm.submit();
                    }
                    else{
                        alert("请选择角色")
                    }
                }
            })
        },
    //更新修改数据初始数据
    loginData() {
      this.updateData = this.copeElement(this.userData);
    },
    //复制对象
    copeElement(obj) {
      var data = {};
      for (var key in obj) {
        data[key] = obj[key];
      }
      return data;
    },
    //判断数据是否完整
    initData(vm) {
      axios
        .post("getUserDate.action")
        .then(function(success) {
          var data = success.data;
          if (data.indexData == false) {
            vm.indexData = false;
            vm.loginDepartSelectData(vm);
          } else if (data.indexData == true) {
            vm.userData = success.data;
            vm.indexData = true;
          }
          vm.loginRoleSelectData(vm);
        })
        .catch(function(err) {});
    },
    //获取角色
    loginRoleSelectData(vm) {
      axios.post("allRole.action").then(
        function(success) {
         vm.selectdata.role = success.data;
        }
      ).catch( function(err) {});
    },
    UpdateData(title){
        var vm=this;
        var data = {
            title: title,
            data: vm.updateData,
        }
         vm.$validator.validateAll().then(function(value){
                if(value){    
                    switch(title){
                        case "updateRole":
                        case "updateDate":http();break;
                        case "updatePwd":if(vm.updateData.newpass==vm.updateData.newPass){
                              http();              
                            }
                            break;
                    }         
                }
            })
       function http(){
            axios.post("UserDate.action",JSON.stringify(data)).then(function (success) {
            vm.loginData();
            vm.upDate = false;
            vm.upDatePass = false;
            alert("修改成功");
        }, function (err) {
            console.log(err)
            alert("修改失败");
        });
       }
    }
  }
};
</script>
<style lang="css" scoped>
.nav-list > li {
  font-size: 2rem;
  font-family: "黑体";
  height: 4rem;
  line-height: 4rem;
}
#user_data {
  position: relative;
}
#user_data.container {
  padding: 0;
}
.back_box {
  position: absolute;
  top: 0rem;
  width: 100%;
  height: 95%;
  padding-bottom: 25px;
  background: rgba(178, 180, 182, 0.25);
}
.back_box > div {
  position: relative;
  top: 2rem;
  padding: 0;
}
.back_box form {
  background: #fff;
}
.back_box .text-alert {
  text-align: center;
}
</style>




