<template>
  <div class="panel panel-info">
    <div class="panel-heading">
      <h3 class="panel-title">
        <span v-text="statestitle"></span>信息</h3>
    </div>
    <div class="panel-body">
      <div v-if="page.datas!=[]">
        <table class="table table-bordered table-striped table-hover">
          <thead>
            <tr>
              <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(datas,index) in page.datas" :key="index">
              <td v-for="(data,index) in datas" :key="index" v-text="data"></td>
              <td>
                <button v-for="(tablework,index) in page.tableworks" :key="index" v-text="tablework.title" :class="tablework.class"></button>
                <!--@click="loadData(datas,tablework.callbacks,$event)"-->
              </td>
            </tr>
          </tbody>
        </table>
        <!-- 页码 -->
        <ul class="pagination col-sm-12">
          <div class="col-sm-3">
            <a class="btn disabled" v-html="'位于第'+page.active+'页'"></a>
            <a class="btn disabled" v-html="'共'+page.pageTotal+'页'"></a>
          </div>
          <li @click="pageClick(pages)" v-for="pages in page.pages" :key="pages" :class="{'active':page.active==pages}">
            <!--class="{{addClass(pages)}} :class="{true:'active'}[page.active==pages]""-->
            <a href="javascript:void(0);" v-text="pages"></a>
          </li>
          <div class="col-sm-4">
            <div class="col-sm-4">
              <select class="form-control" v-model="page.pagelist" @change="countclick">
                <option v-for="(item,index) in page.pageselect" :key="index" :value="item" v-text="item" :selected="item==page.count"></option>
              </select>
            </div>
            <div class="col-sm-3 loginpage">
              <input type="text" class="form-control" v-model="loginpage" placeholder="前往页数" style="font-size:6px">
            </div>
            <div class="col-sm-2">
              <button class="btn btn-info" @click="pageClick(loginpage)">前往</button>
            </div>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data: function () {
      return {
        page: {
          titleData: [], //表格标题数组
          datas: [], //表格数据数组
          pages: [], //页码数组
          pageselect: [2, 5, 10], //可选择每页显示条数数组
          active: 1, //当前页
          pagelist: 5, //每页显示数据条数
          pageTotal: 0, //总页数
          listTotal: 0, //总条数
          count: 5 //页码可显示数，即页码数组长度
        },
        loginpage: "",
       
      };
    },
    props: ["stateId", "childId","statestitle"],
    mounted: function () {
      this.loginclick();
    },
    methods: {
      //选择每页显示多少行数据函数
      countclick() {
        this.allcheck = false;
        this.page.active = 1;
        this.loginclick(this);
      },
      //页面请求函数
      loginclick() {
        var vm = this;
          axios.post(
              "selectPaging.action",
             {
                page: {
                  active: vm.page.active,
                  pagelist: vm.page.pagelist
                },
                state: {
                  id: vm.stateId,
                  ChildId: vm.childId
                }
              }
            )
            .then(function(success) {
                vm.page.titleData = judgeTitle(success.data.data_title);
                vm.page.datas = judgeData(success.data.lists);
                vm.page.listTotal = success.data.listTotal;
                vm.page.pageTotal = Math.ceil(vm.page.listTotal / vm.page.pagelist);
            })
            .catch(function(error) {
                console.log(error);
                vm.page.datas = '';
            });
      },
      //页面点击函数
      pageClick(page) {
        var Active = page; //将要前往页面
        var active = this.page.active; //当前页面
        var total = this.page.pageTotal; //总页面
        if (Active == active) {
          this.loginpage = "";
          return;
        } else if (Active > total) {
          alert("没有第" + Active + "页");
          this.loginpage = "";
          return;
        } else if (Active == "首页") {
          Active = 1;
        } else if (Active == "尾页") {
          Active = total;
        } else if (Active == "«") {
          if (active == 1) {
            return;
          }
          Active = active - 1;
        } else if (Active == "»") {
          if (active == total) {
            return;
          }
          Active = active + 1;
        } else if (Active < 1) {
          Active = 1;
        }
        this.loginpage = "";
        if (this.loginclick && typeof this.loginclick === "function") {
          this.page.active = Active;
          this.loginclick();
        }
      },
      //过滤表格title
      judgeTitle(data) {
        var datas = data;
        for (var i = 0; i < datas.length; i++) {
          if (datas[i] == "密码") {
            // var b = datas.slice(0, i);
            // var d = datas.slice(i + 1, datas.length);
            // datas = b.concat(d);
            datas = datas.splice(i, 1);
          }
        }
        return datas;
      },
      //过滤函数
      judgeData(data) {
        var datas = data;
        if (datas.length > 0) {
          datas.forEach(function (Data) {
            for (var key in Data) {
              switch (key) {
                case "password":
                  delete Data[key];
                  break;
                case "regtime":
                  Data[key] = datafilter(Data[key], "yyyyMMdd");
                  break;
              }
            }
          });
        } else {
          datas = "";
        }
        return datas;
      }
    },
    watch: {
      stateId: function (news, olds) {
        this.loginclick();
      }
    }
  };
</script>

<style thisd>


</style>



