<template>
    <div>
         <div class="col-sm-12" v-if="childId=='add'">
        <div class="panel panel-info">
            <div class="panel-heading">
                <h3 class="panel-title"><span v-text="statestitle"></span>添加</h3>
            </div>
            <div class="panel-body">
                <form class="form form-horizontal col-sm-8" name="myform">
                    <div class="form-group" >
                        <label class="control-label col-sm-2"><span v-text="statestitle"></span>名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('myname')&&addData.name!='','has-error':errors.has('myname')}">
                            <input type="text" class="form-control" name="myname" :placeholder="'请输入'+statestitle+'名,最多为20位'" v-model="addData.name" :data-vv-as="statestitle+'名'"
                            @blur="verNameData(addData.name,'name')" v-validate="'required|cnlength:20'">
                            </div>
                             <div class="text-danger">
                                 <span v-show="errors.has('myname')">{{errors.first("myname")}}</span>
                            <!-- <span >已存在</span> -->
                        </div>
                    </div>
                    <div class="form-group" v-show="stateId=='gongnengguanli'">
                        <label class="control-label col-sm-2">功能url</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('myurl')&&addData.url!='','has-error':errors.has('myurl')}">
                            <input type="text" name="myurl" class="form-control" placeholder="请输入功能url,只能输入英文和数字" v-model="addData.url" @blur="verNameData(addData.url,'url')"
                              data-vv-as="url"  v-validate="'required|alpha_num'"></div>
                             <div  class="text-danger">
                            <span v-show="errors.has('myurl')">{{errors.first("myurl")}}</span>
                            <!-- <span >已存在</span> -->
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">详情描述</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="请输入描述信息，最多不超过50个字符" v-model="addData.detail" data-vv-as="描述"
                            v-validate="'required|cnlength:50'"></textarea>
                            </div>
                         <div class="text-danger">
                          <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-3 col-sm-offset-3 btn btn-info" @click="addDatas">提交</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
export default {
    data:()=>{
        return {
            addData:{
                id:"",
                name:"",
                url:"",
                detail:""
            }
        }
    },
 props: ["stateId", "childId","statestitle"],
 methods:{
     //数据添加操作
     addDatas:function () {
         var vm=this;
         var stateId=vm.stateId;
         var data=vm.addData;
         var datas = {
                data:data ,
                states: {
                    id: stateId
                }
         }
         vm.$validator.validateAll().then(function(value){
            if(value){    
                    axios.post("distributor.action",datas).then(function (success) {
                        alert("添加成功");
                        data={};
                        location.href="/main/ "+ stateId;
                        }).catch( function (error) {
                            alert("添加失败");
                    })
            }
        })
       
    },
    //功能，战区，角色验证添加
   verNameData:function (datas,title) {
       var vm=this;
        var data = {
            data: {
                data:datas,
                name:title
            },
            states: {
                id: vm.stateId
            }
        }
        if(arguments[0]){
            axios.post("inspectData.action", data).then(function (success) {
                if(success.data.msg!=""){
                    alert("已存在");
                }
            }).catch(function (error) {
            });
        }
    }
 }
}
</script>





