<template>
    <div class="list-group" >
        <a class="list-group-item list-group-item-heading list-item-head">模块</a>
        <router-link  class="list-group-item" v-for="menu in menus" active-class="active" :key="menu.href" :to="'/main/'+menu.href"  v-text="menu.title"></router-link>
</div>
</template>
<script>
export default {
  data: function() {
    return {
      menus: []
    };
  },
  created: function() {
    var vm = this;
    vm.loginmenu(vm);
  },
  methods: { 
    loginmenu: function(vm) {
      axios
        .get("static/json/menu.json")
        .then(function(response) {
          vm.menus = response.data.menu;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  }
};
</script>
<style scoped>
.active{
  z-index: 2;
  color: #fff;
  background-color: #337ab7;
  border-color: #337ab7;
}
</style>




