<template>
   <div>
       
       <!-- <router-view name="table"></router-view> -->
       <div class="col-sm-6">
            <ul class="nav nav-tabs nav-justified ">
                <!--子菜单生成-->
                <li v-for="(ChildMenu,index) in childMenus" :key="index"  :class="{active:componentId === ChildMenu.href}" >
                    <a href="javascript:void(0);" class="btn btn-link" @click="componentId=ChildMenu.href"><span v-text="statesTitle"></span><span v-text="ChildMenu.title"></span></a></li>
            </ul>
        </div>
      <div v-if="stateId" class="col-sm-12">
             <component :is="componentId" :stateId="stateId" :childId="childId" :statestitle="statesTitle"></component>
      </div>
   </div>
</template>
<script>
import defaultPanel from '@/components/public/table'
import addinforPanel from '@/components/public/addinfor'
export default {
    name:"index",
    components:{
        'selectok':defaultPanel,
        "add":addinforPanel,
        'selectwill':defaultPanel
        },
    data:function(){
        return {
            stateId:"",
            childId:"",
            childMenus:[],
            componentId:"selectok",
            statesTitle:"",
        }
        },
    mounted:function(){
        this.loginhref();
        },
    methods:{
        //转为主页
        loginhref (){
                var vm=this;
                if(vm.stateId==""){
                location.href="#/main"
                }
            },
        //获取子标题
        ChildHttp () {
                var vm=this;
                axios.get("static/json/menu.json").then(function (success) {
                    vm.childMenus = success.data.childmenu[vm.stateId];
                }).catch(function (err) {
                    console.log(err);
                })
            },
        //初始化href数据
        stateUpdateWatch  (mainID) {
                var vm=this;
                vm.componentId="selectok";
                if (mainID != "") {
                vm.stateId = mainID;
                    vm.ChildHttp();
                    if (vm.componentId != "") {
                    vm.childId = vm.componentId;
                    } else {
                    vm.childId = "";
                    }
                } else {
                vm.stateId = "";
                }
                switch (vm.stateId) {
                    case "jueseguanli":
                    vm.statesTitle = "角色";
                        break;
                    case "gongnengguanli":
                    vm.statesTitle = "功能";
                        break;
                    case "yonghuguanli":
                    vm.statesTitle = "用户";
                        break;
                }
            }
            },
    watch: {
            '$route' (to, from) {
            //   // 对路由变化作出响应...
            this.stateUpdateWatch(to.params.stateid);
            },
            "componentId"(to,from){
                this.childId=to;
            }
        },
}
</script>
<style scoped>
.active a{
    background: #ccc!important;
}
</style>





